<template>
    <section class="training">
        <app-header></app-header>
        <div class="training__img">
            <div class="training__img__wrapper">
                <!--<img src="/assets/img/core_training.jpg" alt="Dejan Vukojevi | Osnovna obuka">-->
                <h3 class="training__img__subheading">Napredne tehnike rukovanja orujem</h3>
                <h1 class="training__img__heading">Progresivna obuka</h1>
            </div>
        </div>
        <div class="training__desc">
            <p>Nakon uspeno savladane osnovne obuke, pojedinac ili grupa prelazi na progresivni nivo obuke, koji podrazumeva sloeniju  dinamiku i povezivanje svih elemenata steenih u programu osnovne obuke u jednu celinu. U programu progresivne obuke, pored elemenata definisanih programom osnovne obuke ukljuuje se taktika,  brzo reagovanja, razmiljanje, odluivanje, pojaana psihika i fizika aktivnost, upotrena i manipulacija vatrenim orujem i resavanje situacija u veoma kratkom vremenskom periodu. Dobra preporuka je da se pojedinac ili grupa u progresivnoj obuci, poveu sa drugim programima kao to su IPSC i IDPA programi, sa kojima mogu praktino da svoja steena znanja provere  i u  takmiarskom smislu, koja su usko povezana sa temom progresivne obuke.
            </p>
        </div>
        <app-footer></app-footer>
    </section>
        
</template>

<script>
    import Header from "./components/Header.vue";
    import Footer from "./components/Footer.vue";
    export default {
        components: {
            "app-header": Header,
            "app-footer": Footer
        }

        
    }
